.page {
  font-family: "Manrope", sans-serif;

  margin: 0;
  min-width: 320px;
  overflow-x: hidden;

  scroll-behavior: smooth;

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(100%);
    pointer-events: none;
  }

  &__menu:target {
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
  }

  a {
    text-decoration: none;
  }

}
